@import '../global/mixin';
@import '../global/color';

// 공통
$number: 8rem;
$date: 10rem;
$short-text: 6rem;
$button: 4rem;
// 회원목록
$email: 60%;
$nickname: 100% - $email;
$user-id: 16rem;
// 공연관리
$partner-id: 18%;
$title: 100% - $partner-id;
// 공연등록
$main-image: 10%;
$main-title: 35%;
$line-up: 30%;
$place: 100% - ($main-image + $main-title + $line-up);

._table-row {
  @include display-flex;
  @include flex-direction(row);
  @include flex-wrap(nowrap);
  @include align-items(center);

  width: 100%;
  min-height: 2rem;
  // border
  border-bottom: solid 1px $blue-dark;
  // font
  @include default_font;
  font-size: calc-size(2rem);

  a {
    color: $green-light;
  }

  .number {
    width: $number;
  }
  .date {
    width: $date;
  }
  .short-text {
    width: $short-text;
  }
  .button {
    @include cursor-pointer;
    width: $button;
  }
  // 회원목록
  .email {
    width: $email;
  }
  .nickname {
    width: $nickname;
  }
  .user-id {
    width: $user-id;
  }
  // 공연관리
  .partner-id {
    width: $partner-id;
  }
  .title {
    width: $title;
  }
  // 공연등록
  .main-image {
    width: $main-image;
  }
  .main-title {
    width: $main-title;
  }
  .line-up {
    width: $line-up;
  }
  .place {
    width: $place;
  }
}

#ticket-form {
  input {
    padding: 1rem 0;
  }

  .main-image {
    width: 100%;
    max-height: 20rem;
    object-fit: contain;
  }

  .left-container {
    width: 50%;
    padding: 0 4% 0 0;
  }

  .right-container {
    width: 50%;
    padding: 0 0 0 4%;
  }

  .input-container {
    margin: 0 0 2rem;
    overflow-x: scroll;
  }

  .input-placeholder {
    white-space: nowrap;
    margin: 0 1rem 0 0;
  }

  .add-artist-container {
    position: relative;
    @include border-radius(0.2rem);
    @include size(8rem, 16rem);
    padding: 2rem 1rem 1rem;
    margin: 0 1rem 0 0;
    background: rgba(256, 256, 256, 0.2);
    img {
      @include size(6rem);
      @include border-radius(3rem);
      object-fit: cover;
      overflow: hidden;
    }

    .remove-button {
      position: absolute;
      top: 0.5rem;
      right: 0.5rem;
      @include cursor-pointer;
    }
  }

  .add-artist-button {
    @include size(4rem);
    @include border-radius(2rem);
    @include cursor-pointer;
    background: rgba(256, 256, 256, 0.2);
    color: $white;
  }

  .submit-button {
    @include cursor-pointer;
    @include size(20%, 4rem);
    margin-top: 8%;
    background: $green-light;
    border: none;
    @include border-radius(0.2rem);
  }
}

._title {
  color: $white;
  font-weight: $fw-semi-bold;
}

._clickable-body {
  @include cursor-pointer;
  color: $white-clear;
  font-weight: $fw-normal;
}

._body {
  color: $white-clear;
  font-weight: $fw-normal;
}



